@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
* {
  margin: 0;
  padding: 0; }

html {
  font-size: 10px; }

.global-wrapper {
  display: flex; }

.block {
  width: 100wv; }

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.logo {
  font-size: 5rem;
  margin: 6.5rem 0rem 10rem 10rem;
  border: 0.3rem solid #000000;
  border-radius: 0.5rem;
  padding: 0.5rem 2rem 0.5rem 2rem;
  cursor: pointer; }

.fa-bars {
  margin: 7rem 10rem 6rem 0rem;
  cursor: pointer; }

li:nth-of-type(2n-1) {
  background: #ebebeb; }

p::first-letter {
  color: #dd7d00; }

.h1 {
  font-size: 6rem;
  margin: -3rem 60rem 4rem; }

.front-page-text {
  font-size: 2rem;
  margin: 1rem 50rem 9rem;
  text-align: center;
  color: #797979c0; }

form {
  max-width: 420px;
  margin: 2rem 3rem 3rem 8rem;
  padding: 3rem 4rem;
  background: white;
  box-shadow: 0 0 3rem rgba(0, 0, 0, 0.5); }

.form-title {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 2.5rem;
  color: #757575;
  margin: 1rem 3rem 1rem 12rem; }

.feedback-input {
  color: #000000;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  background-color: transparent;
  border-bottom: 2px solid #757575;
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  transition: all 0.3s;
  padding: 13px;
  margin-bottom: 15px;
  width: 100%;
  box-sizing: border-box;
  outline: 0; }

.feedback-input:focus {
  border-bottom: 2px solid #CC4949; }

textarea {
  height: 150px;
  line-height: 150%;
  resize: vertical; }

[type="submit"] {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  padding: 1rem 2rem;
  margin: 1rem 15rem 1rem 15rem;
  background: #dd3f00;
  border-radius: 5rem;
  border: 0;
  cursor: pointer;
  color: white;
  font-size: 24px;
  transition: all 0.3s;
  font-weight: 700; }

[type="submit"]:hover {
  background: #8a4e00; }

.h2 {
  font-size: 6rem;
  text-align: center;
  margin: 15rem 43rem 6rem 45rem; }

.second-page-text {
  font-size: 2rem;
  margin: 0rem 30rem 4rem 30rem;
  text-align: center;
  color: #797979c0; }

.task-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 6rem 30rem 19rem; }

.task {
  box-shadow: 0 0 2rem rgba(0, 0, 0, 0.5);
  text-align: center;
  margin: 1rem 3.5rem;
  background: rgba(224, 18, 18, 0.801); }

.task-title {
  font-size: 2rem;
  margin: 3rem;
  color: #ffffff; }

.task-text {
  font-size: 1.6rem;
  line-height: 2.6rem;
  font-weight: lighter;
  padding: 2rem 3.5rem;
  color: #ffffff; }

.know-more-button {
  font-size: 2rem;
  border: 0px solid black;
  border-radius: 3rem;
  background: #ffffff;
  color: #616161;
  margin: 1rem 3rem -5rem;
  cursor: pointer; }

.fa-diamond {
  color: #ffffff;
  padding-top: 5rem; }

.fa-plus {
  color: #616161;
  margin: 1rem 0; }

.h3 {
  font-size: 6rem;
  text-align: center;
  margin: 10rem 43rem 0rem 46rem;
  line-height: 15.5rem; }

.third-page-text {
  font-size: 2rem;
  margin: 5rem 40rem 4rem;
  text-align: center;
  color: #797979c0; }

.works-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 4rem 15.6rem 20rem; }

.works {
  width: 500px;
  height: 500px;
  background: #e01212;
  margin: 1.5rem; }

.h4 {
  font-size: 6rem;
  text-align: center;
  margin: 0rem 43rem 0rem 46rem;
  line-height: 15.5rem; }

.fourth-page-text {
  font-size: 2rem;
  margin: 5rem 40rem 4rem;
  text-align: center;
  color: #797979c0; }

.column-wrapper {
  display: flex;
  margin: 8rem 15rem 6rem 26rem; }

.column:nth-of-type(1) {
  flex: 4 200px;
  display: flex;
  flex-flow: column; }

.element-wrapper {
  flex: 1 auto;
  font-size: 18px;
  line-height: 1.5;
  margin: 1rem;
  padding: 1rem;
  border: 0px solid black;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.5); }

.app-title {
  font-size: 4rem;
  text-align: center; }

.app-text {
  font-size: 2rem;
  margin: 2rem 10rem 4rem 18rem;
  color: #797979c0; }

img {
  height: 50rem;
  width: 50rem; }

.know-more {
  font-size: 2rem;
  border: 0px solid black;
  border-radius: 3rem;
  background: #ffffff;
  color: #616161;
  padding: 0.5rem 2rem;
  cursor: pointer;
  box-shadow: 0 0 0.3rem rgba(0, 0, 0, 0.5);
  margin: 3rem 50rem 1rem 18rem; }

.fa-plus {
  color: #616161;
  margin: 1rem 2rem 1rem 0; }

.h5 {
  font-size: 6rem;
  text-align: center;
  margin: 1rem 43rem 2rem 46rem;
  line-height: 15rem; }

.fifth-page-text {
  font-size: 2rem;
  margin: -5rem 40rem 4rem;
  text-align: center;
  color: #797979c0; }

.units-wrapper {
  display: flex;
  justify-content: space-between;
  margin: 0 30rem; }

.units {
  box-shadow: 0 0 2rem rgba(0, 0, 0, 0.5);
  text-align: center;
  margin: 1rem 3.5rem; }

.sides {
  background: #f04242; }

.center {
  background: rgba(240, 66, 66, 0.349); }

.button {
  font-size: 2.5rem;
  border: 0px solid black;
  background: #ffffff;
  color: #616161;
  cursor: pointer;
  margin: 40rem 10rem -1.5rem 10rem;
  padding: 2rem 4rem; }

.button-title {
  font-size: 1.3rem;
  color: #616161;
  margin: -3rem 2rem -1rem 2rem;
  line-height: 3rem; }

.cv-text {
  display: flex;
  flex-direction: column;
  margin: 10rem; }
